

.container{
  display: flex;
  width: 100%;
}
@media (max-width: 710px) {
  .container{
    display: flex;
    flex-direction: column-reverse;
    width: 100%;
  }
  .image{
    width: 90vw;
    border-radius: 20px;
  }
  .right-half{
    padding: 20px;
    width: 100vw;
    
  }
  .left-half{
    padding: 20px;
    width: 100vw;
  }
}
.left-half{
  padding: 20px;
  width: 50vw;
  position: relative;
  top:50px;
}
.right-half{
  padding: 20px;
  width: 50vw;
  
}
@media (min-width: 1210px) {
  .emptySpace{
    width: 400px;
  }
}

.image{
  height: 90vh;
  width: 650px;
  max-width: 50vw;
  border-radius: 20px;
}


/* shared input feild */
.input{
  min-width: 300px;
  height: 25px;
  border-radius: 6px;
  padding: 4px;
  font-size: 14px;
  border: 1px solid #d4d4c4;
  margin-top: 5px;
}
.input:focus {
  border-color: #999; /* Subtle change */
  box-shadow: none; /* Remove shadow */
  outline: none;
}
b{
  font-weight: 500;
  color: #596863;
}
p{
  margin: 0px;
}
.margin-Top{
  margin-top: 10px;
}
.submit-button{
  width: 311px;
  height: 50px;
  background-color: #243c67;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 17px;
  margin-top: 20px;
}